  margin: 0;
  font-family: "Poppins", sans-serif;
  background: #0f1115;
  color: #f2f2f2;
  scroll-behavior: smooth;
}

h1, h2, h3 {
  font-weight: 600;
  margin-bottom: 1rem;
}

/* NAVBAR */
.navbar {
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  background: rgba(0, 0, 0, 0.8);
  transition: all 0.3s ease;
  z-index: 10;
}
.navbar .logo {
  color: #f2f2f2 !important;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.4rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
}
.navbar .logo i {
  color: #4ab4a3;
  font-size: 1.5rem;
}
.navbar .logo:hover {
  color: #4ab4a3;
}
.navbar nav {
  display: flex;
  align-items: center;
}
.navbar nav a {
  color: #f2f2f2;
  margin: 0 0.5rem;
  text-decoration: none;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  transition: all 0.3s ease;
}
.navbar.shrink {
  padding: 0.5rem;
  font-size: 0.9rem;
}

.hamburger {
  display: none;
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #f2f2f2;
  cursor: pointer;
}

/* Progress bar */
.progress-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 4px;
  background: #4ab4a3;
  width: 0%;
}

/* Active link highlight */
nav a.active {
  background: #4ab4a3;
  color: #0f1115;
  box-shadow: 0 0 10px #4ab4a3, 0 0 20px #4ab4a3;
  font-weight: bold;
}

/* HERO  */
.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 2rem;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center/cover;
  background-attachment: fixed;
  animation: fadeIn 2s ease;
  z-index: 0;
}
.hero::before {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 0;
}
.hero h1, .hero p, .hero .btn {
  margin: 0.5rem 0;
  position: relative;
  z-index: 1;
  text-align: center;
}

.btn {
  background: #4ab4a3;
  color: #0f1115;
  padding: 0.6rem 1.2rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  transition: transform 0.2s;
}
.btn:hover {
  transform: scale(1.1);
}

/*  BIOLOGY  */
.about {
  padding: 3rem 2rem;
  text-align: center;
}
.about .bio-grid {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1.5rem;
}
.about .bio-card {
  background: #1a1f25;
  padding: 1.2rem;
  border-radius: 10px;
  flex: 1 1 250px;
  max-width: 300px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  transition: transform 0.3s ease;
}
.about .bio-card:hover {
  transform: translateY(-5px);
}
.about .bio-card i {
  font-size: 2rem;
  color: #4ab4a3;
  margin-bottom: 0.5rem;
}

/*  CAROUSEL*/
.carousel-section {
  text-align: center;
  padding: 3rem 2rem;
}
.carousel-section .carousel {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  max-width: 900px;
  margin: auto;
}
.carousel-section .carousel .carousel-track {
  display: flex;
  transition: transform 0.5s ease;
  width: 100%;
}
.carousel-section .carousel .slide {
  min-width: 100%;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
.carousel-section .carousel .slide i {
  font-size: 2rem;
  color: #4ab4a3;
  margin-bottom: 0.5rem;
}
.carousel-section .carousel .slide img {
  width: 100%;
  max-width: 700px;
  height: 350px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 10px;
  margin-bottom: 0.8rem;
}
.carousel-section .carousel .slide p {
  max-width: 700px;
  text-align: center;
  font-size: 1rem;
  line-height: 1.5;
}
.carousel-section .carousel button.prev,
.carousel-section .carousel button.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.5);
  border: none;
  color: white;
  font-size: 2rem;
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  z-index: 5;
  transition: background 0.3s;
}
.carousel-section .carousel button.prev:hover,
.carousel-section .carousel button.next:hover {
  background: #4ab4a3;
}
.carousel-section .carousel button.prev {
  left: 10px;
}
.carousel-section .carousel button.next {
  right: 10px;
}

/* VIDEO */
.video {
  text-align: center;
  padding: 3rem 2rem;
}
.video video {
  max-width: 80%;
  border-radius: 10px;
}

/* RESEARCH*/
.modal-section {
  padding: 4rem 2rem;
  min-height: 50vh;
  text-align: center;
  background: #1a1f25;
}
.modal-section p {
  max-width: 700px;
  margin: 0 auto 2rem;
  font-size: 1.1rem;
  line-height: 1.6;
}
.modal-section .research-buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
}

/*  CONTACT */
.contact {
  text-align: center;
  padding: 3rem 2rem;
}
.contact form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin: auto;
}
.contact form input, .contact form textarea {
  margin: 0.5rem 0;
  padding: 0.6rem;
  border-radius: 5px;
  border: none;
  font-family: "Poppins", sans-serif;
}

/* FOOTER  */
footer {
  text-align: center;
  padding: 1rem;
  background: #111;
}
footer .socials a {
  color: #f2f2f2;
  margin: 0 0.5rem;
  font-size: 1.2rem;
}

/*  MODAL  */
.modal {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal[aria-hidden=true] {
  display: none;
}
.modal .modal-content {
  background: #f2f2f2;
  color: #0f1115;
  padding: 2rem;
  border-radius: 10px;
  max-width: 700px;
  position: relative;
}
.modal .close {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/*  RESPONSIVE  */
@media (max-width: 1024px) {
  .hamburger {
    display: block;
  }

  nav {
    display: none;
    flex-direction: column;
    width: 100%;
    text-align: center;
    background: rgba(0, 0, 0, 0.9);
  }
  nav a {
    display: block;
    padding: 1rem;
    margin: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }

  nav.active {
    display: flex;
  }
}
@media (max-width: 768px) {
  .bio-grid {
    flex-direction: column;
  }

  .carousel-track {
    width: 100%;
  }
}`, "",{"version":3,"sources":["webpack://./css/main.scss"],"names":[],"mappings":"AAIA;EACE,SAAA;EACA,kCAAA;EACA,mBANK;EAOL,cANM;EAON,uBAAA;AAHF;;AAMA;EACE,gBAAA;EACA,mBAAA;AAHF;;AAMA,WAAA;AACA;EACE,gBAAA;EACA,MAAA;EACA,WAAA;EACA,aAAA;EACA,8BAAA;EACA,mBAAA;EACA,eAAA;EACA,8BAAA;EACA,yBAAA;EACA,WAAA;AAHF;AAKE;EACE,yBAAA;EACA,qBAAA;EACA,gBAAA;EACA,iBAAA;EACA,aAAA;EACA,mBAAA;EACA,WAAA;AAHJ;AAKI;EACE,cAxCE;EAyCF,iBAAA;AAHN;AAMI;EACE,cA7CE;AAyCR;AAQE;EACE,aAAA;EACA,mBAAA;AANJ;AAOI;EACE,cAnDE;EAoDF,gBAAA;EACA,qBAAA;EACA,sBAAA;EACA,kBAAA;EACA,yBAAA;AALN;AASE;EACE,eAAA;EACA,iBAAA;AAPJ;;AAYA;EACE,aAAA;EACA,gBAAA;EACA,YAAA;EACA,iBAAA;EACA,cAxEM;EAyEN,eAAA;AATF;;AAYA,iBAAA;AACA;EACE,kBAAA;EACA,SAAA;EACA,OAAA;EACA,WAAA;EACA,mBApFM;EAqFN,SAAA;AATF;;AAYA,0BAAA;AACA;EACE,mBA1FM;EA2FN,cA1FK;EA2FL,8CAAA;EACA,iBAAA;AATF;;AAYA,UAAA;AACA;EACE,kBAAA;EACA,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,iBAAA;EACA,aAAA;EACA,0EAAA;EACA,4BAAA;EACA,yBAAA;EACA,UAAA;AATF;AAWE;EACE,WAAA;EACA,kBAAA;EACA,QAAA;EACA,8BAAA;EACA,UAAA;AATJ;AAYE;EACE,gBAAA;EACA,kBAAA;EACA,UAAA;EACA,kBAAA;AAVJ;;AAcA;EACE,mBA/HM;EAgIN,cA/HK;EAgIL,sBAAA;EACA,YAAA;EACA,kBAAA;EACA,eAAA;EACA,gBAAA;EACA,0BAAA;AAXF;AAYE;EAAU,qBAAA;AATZ;;AAYA,cAAA;AACA;EACE,kBAAA;EACA,kBAAA;AATF;AAWE;EACE,aAAA;EACA,WAAA;EACA,uBAAA;EACA,eAAA;EACA,kBAAA;AATJ;AAYE;EACE,mBAAA;EACA,eAAA;EACA,mBAAA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,uCAAA;EACA,+BAAA;AAVJ;AAYI;EAAU,2BAAA;AATd;AAWI;EACE,eAAA;EACA,cArKE;EAsKF,qBAAA;AATN;;AAcA,aAAA;AACA;EACE,kBAAA;EACA,kBAAA;AAXF;AAaE;EACE,kBAAA;EACA,aAAA;EACA,mBAAA;EACA,uBAAA;EACA,gBAAA;EACA,gBAAA;EACA,YAAA;AAXJ;AAaI;EACE,aAAA;EACA,+BAAA;EACA,WAAA;AAXN;AAcI;EACE,eAAA;EACA,cAAA;EACA,aAAA;EACA,sBAAA;EACA,uBAAA;EACA,mBAAA;EACA,aAAA;AAZN;AAcM;EACE,eAAA;EACA,cA1MA;EA2MA,qBAAA;AAZR;AAeM;EACE,WAAA;EACA,gBAAA;EACA,aAAA;EACA,oBAAA;KAAA,iBAAA;EACA,mBAAA;EACA,qBAAA;AAbR;AAgBM;EACE,gBAAA;EACA,kBAAA;EACA,eAAA;EACA,gBAAA;AAdR;AAkBI;;EAEE,kBAAA;EACA,QAAA;EACA,2BAAA;EACA,8BAAA;EACA,YAAA;EACA,YAAA;EACA,eAAA;EACA,eAAA;EACA,oBAAA;EACA,kBAAA;